/*-----------------------------------------------------------------*/

/*                      PRELOADER                                  */

/*-----------------------------------------------------------------*/

.preloader-outer {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: #0C0C0C;
  z-index: 6;
  overflow: hidden;
}

.preloader {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 80px;
  height: 60px;
  margin: -30px 0 0 -40px;
}

.preloader .lines {
  width: 80px;
  height: 40px;
  position: absolute;
}

.preloader .lines .line {
  width: 80px;
  height: 1px;
  background-color: #fff;
  position: absolute;
  clip: rect(0, 0, 20px, 0);
}

.preloader .lines .line.line-1 {
  top: 0;
  -webkit-animation: slide 2s ease 0.1s infinite;
          animation: slide 2s ease 0.1s infinite;
}

.preloader .lines .line.line-2 {
  top: 15px;
  -webkit-animation: slide 2s ease .25s infinite;
          animation: slide 2s ease .25s infinite;
}

.preloader .lines .line.line-3 {
  top: 30px;
  -webkit-animation: slide 2s ease .5s infinite;
          animation: slide 2s ease .5s infinite;
}

.preloader .loading-text {
  position: absolute;
  top: 50px;
  text-align: center;
  width: 100%;
  color: #fff;
  font-size: 14px;
  font-family: "Montserrat", sans-serif;
  letter-spacing: 1px;
  line-height: 10px;
  height: 10px;
  text-transform: uppercase;
}

@-webkit-keyframes slide {
  0% {
      clip: rect(0, 0, 20px, 0);
  }
  30% {
      clip: rect(0, 80px, 20px, 0);
  }
  50% {
      clip: rect(0, 80px, 20px, 0);
  }
  80% {
      clip: rect(0, 80px, 20px, 80px);
  }
  100% {
      clip: rect(0, 80px, 20px, 80px);
  }
}

@keyframes slide {
  0% {
      clip: rect(0, 0, 20px, 0);
  }
  30% {
      clip: rect(0, 80px, 20px, 0);
  }
  50% {
      clip: rect(0, 80px, 20px, 0);
  }
  80% {
      clip: rect(0, 80px, 20px, 80px);
  }
  100% {
      clip: rect(0, 80px, 20px, 80px);
  }
}

@-webkit-keyframes fade {
  0% {
      opacity: 1;
  }
  50% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}

@keyframes fade {
  0% {
      opacity: 1;
  }
  50% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}

