  -webkit-transform: translateY(220px);
  transform: translateY(220px);
  -webkit-transition: all .3s ease-in-out;
  -o-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

.works .item-outer .item img {
  max-width: 100%;
  height: auto;
  -ms-transform: scale(1);
  -webkit-transform: scale(1);
  transform: scale(1);
  -webkit-transition: all .3s ease-in-out;
  -o-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

.works .item-outer .item .overlay {
  background: rgba(12, 12, 12, 0.85);
  height: 100%;
  left: 0;
  opacity: 0;
  top: 0;
  position: absolute;
  width: 100%;
  z-index: 1;
  -webkit-transition: all .3s ease-in-out;
  -o-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

.works .item-outer .item:hover h2 {
  -ms-transform: translateY(0px);
  -webkit-transform: translateY(0px);
  transform: translateY(0px);
}

.works .item-outer .item:hover span {
  -ms-transform: translateY(0px);
  -webkit-transform: translateY(0px);
  transform: translateY(0px);
}

.works .item-outer .item:hover .overlay {
  opacity: 1;
}

.works .item-outer .item:hover img {
  -ms-transform: scale(1.1);
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.works .item-outer .item:hover .title:before {
  -ms-transform: translate(0px);
  -webkit-transform: translate(0px);
  transform: translate(0px);
}

.works .item-outer .item:hover .title:after {
  -ms-transform: translate(-45px, -45px);
  -webkit-transform: translate(-45px, -45px);
  transform: translate(-45px, -45px);
}

.works .item-outer .mix {
  padding: 5px;
}

/*-----------------------------------------------------------------*/

/*                      SECTION TESTIMONIALS                       */

/*-----------------------------------------------------------------*/

.testimonials .item .thumb {
  border: solid 1px #ECECEC;
  width: 128px;
  height: 128px;
  overflow: hidden;
  border-radius: 50%;
}

.testimonials .item .text span.author {
  color: #B1B1B1;
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
}

/*-----------------------------------------------------------------*/

/*                      SECTION BLOG                               */

/*-----------------------------------------------------------------*/

.blog .item {
  margin-bottom: 30px;
}

.blog .item .thumb {
  height: 180px;
  overflow: hidden;
}

.blog .item .thumb img {
  width: 100%;
  height: auto;
}

.blog .item .text h2 {
  font-size: 18px;
  font-weight: 400;
  margin: 0 0 15px;
}

.blog .item .text p {
  margin: 0 0 15px;
}

.blog .blog-single img {
  margin-bottom: 20px;
}

/*-----------------------------------------------------------------*/

/*                      SECTION CUSTOMERS                          */

/*-----------------------------------------------------------------*/

.customers {
  background: url(http://placehold.it/740x270) center left no-repeat;
  background-size: cover;
}

.customers .customer-carousel {
  z-index: 1;
}

.customers .item {
  margin-right: 24px;
  position: relative;
  z-index: 1;
}

.customers .item img {
  max-width: 100%;
  opacity: .5;
  height: auto;
  -webkit-transition: all .3s ease-in-out;
  -o-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

.customers .item img:hover {
  opacity: 1;
}

.customers .overlay {
  background: rgba(12, 12, 12, 0.85);
  height: 100%;
  left: 0;
  top: 0;
  position: absolute;
  width: 100%;
}

.customers .owl-theme .owl-controls .owl-page span {
  width: 10px;
  height: 10px;
  margin: 5px 3px;
  background: #FFF;
}

.customers .owl-theme .owl-controls {
  margin-top: 24px;
}

/*-----------------------------------------------------------------*/

/*                      OTHER ELEMENTS                             */

/*-----------------------------------------------------------------*/

.more-items {
  margin-top: 25px;
}

.more-items a {
  color: #0C0C0C;
  font-weight: 300;
  text-decoration: none;
}

.more-items a:hover {
  text-decoration: underline;
}

.more-items a i {
  margin-right: 10px;
}

.pagination {
  margin-top: 25px;
  width: 100%;
}

.pagination a {
  color: #1a1a1a;
}

/* === Button === */

.btn-default {
  color: #FFF;
  background-color: #0C0C0C;
  border: 0;
  border-radius: 0;
  font-size: 12px;
  font-weight: 300;
  padding: 5px 16px;
  position: relative;
}

.btn-default:after {
  content: '\f178';
  font-family: 'FontAwesome';
  font-size: 12px;
  top: 0;
  right: -29px;
  padding: 5px 8px;
  position: absolute;
  background: #0C0C0C;
  -webkit-transition: all .8s ease-in-out;
  -o-transition: all .8s ease-in-out;
  transition: all .8s ease-in-out;
  -ms-transform: rotateY(0deg);
  -webkit-transform: rotateY(0deg);
  transform: rotateY(0deg);
}

.btn-default:hover {
  color: #FFF;
  background-color: #0C0C0C;
  border: 0;
}

.btn-default:hover:after {
  -ms-transform: rotateY(360deg);
  -webkit-transform: rotateY(360deg);
  transform: rotateY(360deg);
}

/* === Progress Bar === */

.progress {
  height: 1px;
  margin-bottom: 20px;
  overflow: hidden;
  background-color: #E0E0E0;
  border-radius: 0;
  -webkit-box-shadow: none;
  box-shadow: none;
}

.progress-bar {
  float: left;
  width: 0;
  height: 100%;
  font-size: 12px;
  line-height: 1px;
  color: #fff;
  text-align: center;
  background-color: #0C0C0C;
  -webkit-box-shadow: none;
  box-shadow: none;
  -webkit-transition: width .6s ease;
  -o-transition: width .6s ease;
  transition: width .6s ease;
}

/* === Form Control === */

.form-control {
  display: block;
  width: 100%;
  height: 50px;
  padding: 0;
  font-size: 14px;
  font-weight: 300;
  line-height: 1.6;
  color: #555;
  background-color: #fff;
  background-image: none;
  border-bottom: solid 1px #0C0C0C;
  border-top: 0;
  border-left: 0;
  border-right: 0;
  border-radius: 0;
  -webkit-box-shadow: none;
  box-shadow: none;
}

.form-control:focus {
  border-color: #C7C7C7;
  outline: 0;
  -webkit-box-shadow: none;
  box-shadow: none;
}

.form-group {
  margin-bottom: 25px;
}

/* === Owl Configure === */

.owl-theme .owl-controls .owl-page span {
  width: 10px;
  height: 10px;
  margin: 5px 3px;
  filter: Alpha(Opacity=20);
  opacity: .2;
  background: #0C0C0C;
}

.owl-theme .owl-controls {
  margin-top: 30px;
}

/*-----------------------------------------------------------------*/

/*                      RESPONSIVE                                  */

/*-----------------------------------------------------------------*/

@media only screen and (min-width: 480px) and (max-width: 767px) {
  .works .item-outer .mix {
      width: 50%;
  }
}

@media only screen and (max-width: 991px) {
  .wrapper {
      width: 100%;
  }
  .header-mobile {
      -ms-transform: translateX(-260px);
      -webkit-transform: translateX(-260px);
      transform: translateX(-260px);
      -webkit-transition: all .3s ease-in-out;
      -o-transition: all .3s ease-in-out;
      transition: all .3s ease-in-out;
  }
  .pushed {
      -ms-transform: translateX(0);
      -webkit-transform: translateX(0);
      transform: translateX(0);
  }
  .main-content {
      width: 100%;
      -webkit-transition: all .3s ease-in-out;
      -o-transition: all .3s ease-in-out;
      transition: all .3s ease-in-out;
  }
  .main-pushed {
      -ms-transform: translateX(260px);
      -webkit-transform: translateX(260px);
      transform: translateX(260px);
  }
  .works .item-outer .item {
      height: 280px;
  }
}

@media only screen and (max-width: 767px) {
  .welcome {
      padding: 60px !important;
  }
  .welcome .quote {
      width: initial;
  }
  .facts .fact-item {
      margin-bottom: 20px;
  }
  .resume .resume-item {
      margin-top: 30px !important;
  }
  .blog .item .text {
      margin-top: 25px;
  }
  .main-content section .section-header {
      margin-bottom: 10px;
  }
}

@media only screen and (max-width: 480px) {
  .main-content section {
      padding: 60px 30px;
  }
}

@media only screen and (max-height: 600px) {
  .header .name {
      margin: 15px 0;
  }
  .header .main-nav ul li {
      padding-bottom: 10px;
  }
  .header .social-icons {
      margin: 0 0 15px;
  }
  .header .copyright {
      bottom: -18px;
      position: relative;
  }
}

@media screen and (max-width: 550px) {
  .about .skills .item .skill-info h3 {
    font-size: 11px;
  }
}

.icon-btn {
  background: transparent;
  border: none;
  width: 100%;
  height: 100%;
}

.float-menu {
  z-index: 1000;
  width: 100vw;
  height: 100vh;
}


